.loader {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border: 3px solid #fff;
  border-bottom-color: rgba(0, 0, 0, 0);
  border-radius: 50%;
  animation: rotation 1s linear infinite;
  display: inline-block;
  position: relative;
  top: 50%;
  left: 50%;
}

.loader:after {
  content: "";
  box-sizing: border-box;
  width: 24px;
  height: 24px;
  transform-origin: center;
  border: 3px solid #ff3d00;
  border-top-color: rgba(0, 0, 0, 0);
  border-radius: 50%;
  margin: auto;
  animation: rotationBack .5s linear infinite;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

@keyframes rotationBack {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(-360deg);
  }
}

.loader {
  display: block;
}

.loader.hide {
  display: none;
}

.cat-info {
  width: 700px;
  background-color: #7fffd4;
  border: 2px solid #000;
  border-radius: 4px;
  flex-direction: column;
  align-content: center;
  align-items: center;
  margin: 50px;
  padding: 20px;
  display: flex;
}

.cat-info.hide {
  display: none;
}

.breed-select {
  position: relative;
  left: 50px;
}

